<template>
    <div>
        <div class="pledge-affirm">
            <div ref="betterscroll" class="use-list-scroller">
                <div class="heightCav">
                    <div class="progressbar-container">
                        <ul class="progressbar">
                            <li></li>
                            <li class="active"></li>
                            <li></li>
                        </ul>
                    </div>
                    <div class="scale-container">
                        <div class="pledge-cell">
                            <div class="pledge-e pledge-word"></div>
                            <div class="pledge-e pledge-cash">{{money}}</div>
                            <div class="pledge-dec"> 0-3</div>
                            <div class="pledge-dec"></div>
                        </div>
                        <mt-button size="large" class="wechat-pay-btn" type="default" @click.native="wxPayHandle"></mt-button>
                    </div>
                </div>
                <div class="foot">
                    <p class="phone"><a href="tel:4001368868">4001368868</a></p>
                    <p class="merchant"></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import wxpay from "../../common//js/wxpay";
import BScroll from "better-scroll";
import { mapState } from "vuex";
import { Toast, Button } from "mint-ui";
import Vue from "vue";
import "mint-ui/lib/toast/style.css";
import "mint-ui/lib/button/style.css";
import { savaToLocal, loadFromlLocal } from "../../common/js/storage.js"; // 

Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "pledge-affirm",
  data() {
    return {
      money: 0,
      salesmanid: "",
      payPam: null,
      clearId: -10000
    };
  },
  components: {},
  mounted() {
    let pledgeInfo = loadFromlLocal("aml", "pledgeInfo");
    this.salesmanid = pledgeInfo.salesmanid;
    this.getMoney();
  },
  methods: {
    getMoney() {
      this.$http
        .post(this.path + `php/suncode/dealfuc.php`, {
          fuc: "sets_price",
          dptype: 1
        })
        .then(response => {
          let data = response.body;
          this.money = parseFloat(data);
          this.initScroller();
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    initScroller() {
      this.$nextTick(() => {
        this.betterscroll = new BScroll(this.$refs.betterscroll, {
          click: true
        });
      });
    },
    wxPayHandle(event) {
      if (event._constructed) return;   // betterscrollerclick
      this.$http
        .post(this.path + "wxapi/Pay/example/pay.php", {
          type: "DepositPayParam",
          dptype: 1,
          clientid: this.userid,
          salesmanid: this.salesmanid
        })
        .then(response => {
          let status = response.body.error;
          if (status != "ok") {
            Toast({ message: response.body.msg, duration: 2000 });
            return;
          }
          this.payPam = JSON.parse(response.body.jsapi);
          this.callpay();
        })
        .catch(response => {
          console.log(response);
        });
    },
    callpay() {
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            this.jsApiCall,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", this.jsApiCall);
          document.attachEvent("onWeixinJSBridgeReady", this.jsApiCall);
        }
      } else {
        this.jsApiCall();
      }
    },
    //JSON.parse(payPam.result),
    jsApiCall() {
      WeixinJSBridge.invoke("getBrandWCPayRequest", this.payPam, res => {
        WeixinJSBridge.log(res.err_msg);
        if (res.err_msg == "get_brand_wcpay_request:ok") {
          //to url
          this.updatateUserInfo(this.PaySuccess.bind(this));
          //window.location.href = "../main/myOrder.html";
        }
      });
    },
    PaySuccess() {
      this.clearId = setTimeout(() => {
        this.$router.replace({ path: "/me" });
      }, 1000);
    }
  },
  beforeDestroy() {
    if (this.clearId != -10000) {
      clearTimeout(this.clearId);
    }
    if (this.betterscroll) {
      this.betterscroll.destroy();
    }
  },
  computed: {
    ...mapState(["path", "userid", "phoneno"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.pledge-affirm {
    position: relative;
    height: 100%;

    .use-list-scroller {
        background-color: #fff;
        position: fixed;
        top: 31px;
        left: 0;
        right: 0;
        bottom: 0px;
        overflow: hidden;

        .foot {
            width: 100%;
            position: fixed;
            bottom: 8px;
            text-align: center;

            p {
                font-size: 13px;
                line-height: 17px;
                color: #515151;

                a {
                    color: #000;
                    text-decoration: underline;
                }
            }
        }

        .heightCav {
            box-sizing: border-box;
            position: relative;

            .scale-container {
                position: absolute;
                left: 20px;
                right: 20px;
                height: 100%;
                top: 80px;

                .pledge-cell {
                    width: 100%;
                    margin: 25px 0px;
                    padding-bottom: 5px;
                    color: #303030;
                    position: relative;
                    border-bottom: #acacac 1px solid;

                    .pledge-e {
                        font-size: 19px;
                        font-weight: 700;
                        line-height: 28px;
                        letter-spacing: 1px;
                    }

                    .pledge-cash {
                        color: #FD8709;
                        position: absolute;
                        top: 0px;
                        right: 0px;
                        font-weight: 500;
                    }

                    .pledge-dec {
                        font-size: 12px;
                        line-height: 16px;
                        color: #333;
                    }
                }

                .wechat-pay-btn {
                    background-color: #f8ef94;
                    color: #a2733d;
                    display: block;
                    box-shadow: none;
                    margin-top: 70px;
                    opacity: 0.9;
                    font-size: 0.13rem;
                    letter-spacing: 0.02rem;
                    height: 0.3rem;
                }
            }

            .progressbar-container {
                position: relative;
                width: 100%;

                .progressbar {
                    width: 100%;
                    position: absolute;
                    background-color: #f1f1f1;
                    text-align: center;
                    padding: 15px 0px;
                    overflow: hidden;
                    counter-reset: step;
                    box-shadow: 0 1px 0px rgba(220, 220, 220, 0.1);
                    z-index: -2;
                }

                .progressbar li {
                    list-style-type: none;
                    color: #515151;
                    text-transform: uppercase;
                    font-size: 13px;
                    width: 33.33%;
                    float: left;
                    position: relative;
                }

                .progressbar li.active {
                    color: #515151;
                    font-weight: 700;
                }

                .progressbar li:before {
                    content: counter(step);
                    counter-increment: step;
                    width: 30px;
                    line-height: 30px;
                    display: block;
                    font-size: 14px;
                    color: #515151;
                    background: #fff;
                    border-radius: 100%;
                    margin: 0 auto 8px auto;
                    font-weight: 700;
                }

                .progressbar li:after {
                    content: '';
                    width: 100%;
                    height: 4px;
                    background-color: #fff;
                    position: absolute;
                    left: -50%;
                    top: 13px;
                    z-index: -1; /* put it behind the numbers */
                }

                .progressbar li:first-child:after {
                    /* connector not needed before the first step */
                    content: none;
                }

                /* marking active/completed steps green */
                /* The number of the step and the connector before it = green */
                .progressbar li.active:before, #progressbar li.active:after {
                    background: #8a8a8a;
                    color: #fff;
                    font-size: 15px;
                }
            }
        }
    }
}
</style>



